import React, { Fragment } from "react";
import { Navigation } from "../../lib";
import UWDeal from "./UWDeal";
import Button from "muicss/lib/react/button";
import Expire from "../common/Expire";
import encodeQueryData from "../common/encodeQueryData";
import LazyLoad from "react-lazyload";
import Loader from "../common/Loader";

class UWDealPage extends React.Component {
  constructor(props) {
    super(props);
    this.compsClick = this.compsClick.bind(this);
  }

  dealClick(dealType, dealId, violations, additional_properties, page) {
    this.props.setPageAndScrollPos("/broker-deals/uw", window.scrollY);
    Navigation.go(
      `/broker-deal/${dealType}/${dealId}/${page}?${encodeQueryData({
        violations,
        additional_properties,
        previous_page: window.location.pathname + window.location.search,
      })}`
    );
  }

  addDealNotesClick(dealType, dealId, violations, additional_properties, e) {
    e.stopPropagation();
    Navigation.go(
      `/broker-deal/${dealType}/${dealId}/notes/add?${encodeQueryData({
        violations,
        additional_properties,
      })}`
    );
  }

  compsClick(inNYC, propertyAddress, city, property_type_id, e) {
    e.stopPropagation();
    sessionStorage.setItem("comps_inNYC", inNYC);
    this.props.setPageAndScrollPos("/broker-deals/uw", window.scrollY);
    this.props.submitCompsSearch(
      `${propertyAddress} ${city}`,
      property_type_id
    );
  }

  render() {
    const {
      loading,
      suggestions,
      contact,
      toast,
      loadingContact,
      closeToast,
      updateStar,
      getFullContactInfo,
    } = this.props;
    let successMessage = "";
    if (toast.show) {
      successMessage = (
        <Expire closeToast={closeToast} delay={6000}>
          <div className="success-message">
            <p>{toast.message}</p>
            <Button variant="flat" onClick={closeToast}>
              {toast.success ? "Done" : "Dismiss"}
            </Button>
          </div>
        </Expire>
      );
    }

    var counter = 0;
    return (
      <div>
        {loading ? (
          <Loader />
        ) : (
          <Fragment>
            {suggestions && suggestions.length > 0 ? (
              <div>
                {suggestions.map((item) => (
                  <LazyLoad height={200} offset={100} once>
                    <UWDeal
                      key={counter++}
                      deal={item}
                      contact={contact}
                      updateStar={updateStar}
                      clickFunction={this.dealClick.bind(this)}
                      loadingContact={loadingContact}
                      addDealNoteClick={this.addDealNotesClick.bind(this)}
                      compsClick={this.compsClick}
                      getFullContactInfo={getFullContactInfo}
                    />
                  </LazyLoad>
                ))}
                {successMessage}
              </div>
            ) : (
              <div>
                <div>
                  <div className="grey-circle">
                    <i className="material-icons">warning</i>
                  </div>
                  <h2 className="no-results">No UW Deals</h2>
                </div>
              </div>
            )}
          </Fragment>
        )}
      </div>
    );
  }
}
export default UWDealPage;
